const loanStatus = {
  syncEsopBefore: 10000, // esop
  syncEsopAfter: 11000, // esop
  sendEmail: 20000, // 
  authorize: 20100, // 
  checkBaseInfoOne: 20200, // 1
  checkBaseInfoTwo: 20300, // 2
  checkBaseInfoThree: 20400, // 3
  checkLoanInfo: 20500, // 
  agreeProtocol: 20600, // 
  csReject: 25000, // cs 
  submitSignature: 30000, // &
  syncLoanSystem: 31000, // loan system 
  waitCompanyAppr: 40000, // cs
  waitRiskAppr: 50000, // Risk
  waitGTJAAppr: 60000, // RiskGTJA
  waitClientCfm: 70000, // GTJA
  waitOpsAppr: 80000, // Ops
  syncwaitOpsAppr: 81000, // Ops
  waitFinanceAppr: 90000, // Ops
  waitLoanRelease: 95000, // 
  waitRepay: 100000, // Detail , 
  waitRepayAppr: 101000, // ,
  waitStockAppr: 101100, // 
  waitPayoff: 102000, // 
  waitStock: 110000, // 
  sendStock: 200000, // 
  appExpired: 201000, // 
  clientCancel: 202000, // , 
  syncClientCancel: 202100, // , 
  csPmtReject: 203000, // cs
  riskPmtReject: 204000, // risk
  financePmtReject: 205000, // 
  companyReject: 206000, // 
  gtjaReject: 207000, // GTJA
  gtjaPmtReject: 208000, // GTJA
  noAccount: 7016, // ,
  cancelRefused: 30033, // 
  cancelSucc: 31500 // (,)
}
// 
const repayStatus = {
  auditing: 1000, // 
  traded: 1200, // 
  canceled: 1500, // 
  canceledSys: 1600, // ()
  succeed: 2000, // 
  failed: 3000 // 
}

const repayTitle = {
  waitRepay: '',
  waitRepayAppr: '',
  waitStockAppr: '',
  waitPayoff: '',
  waitStock: '',
  sendStock: ''
}

const repayType = {
  sellStockPrepayment: 1,
  expiredForcedRepayment: 2,
  cashPrepayment: 3
}

const repayMode = {
  sellStockPrepayment: '',
  expiredForcedRepayment: '',
  cashPrepayment: ''
}

// 
const editStatus = [
  loanStatus.sendEmail,
  loanStatus.authorize,
  loanStatus.checkBaseInfoOne,
  loanStatus.checkBaseInfoTwo,
  loanStatus.checkBaseInfoThree,
  loanStatus.checkLoanInfo,
  loanStatus.agreeProtocol,
  loanStatus.csReject
]

// 
const resultStatus = [
  loanStatus.csReject,
  loanStatus.csPmtReject,
  loanStatus.riskPmtReject,
  loanStatus.gtjaReject,
  loanStatus.gtjaPmtReject,
  loanStatus.companyReject
]

const apyResult = {
  // 
  noApy: {navTitle: '', content: '<span style="color: #499EF0;"></span>'},
  csReturn: {navTitle: '', content: '<span style="color: #499EF0;"></span>', canEdit: true},
  // CS Reject , Risk Reject ,GTJA Reject , GTJA Returned
  crgcg: {navTitle: '', content: '<span style="color: #499EF0;"></span>'},
  //  Company Returned , 
  cR: {navTitle: '', content: '<span style="color: #499EF0;"></span>'},
  // 
  noApply: {navTitle: '', content: '1<span style="color: #499EF0;"></span>'},
  // 
  noAccount: {navTitle: '', content: '<span style="color: #499EF0;"></span>', button: ''}
}

const agreeResult = {
  // 
  success: {navTitle: '', content: '', button: ''},
  // 
  confirm: {navTitle: '', content: '', button: ''},
  // 
  fail: {navTitle: '', content: '<span style="color: #499EF0;"></span>', button: ''},
  // 
  cashCheck: {navTitle: '', content: '', button: ''},
  // 
  stockCheck: {navTitle: '', content: '', button: ''},
  // 
  cancelRefused: {navTitle: '', content: '', subContent: '', button: ''},
  // 
  cancelSucc: {navTitle: '', content: '', subContent: '', button: ''}
}

function statusToResult (status) {
  if (status === loanStatus.csReject) {
    // CS Return
    return apyResult.csReturn
  } else if (status === loanStatus.csPmtReject || status === loanStatus.riskPmtReject || status === loanStatus.gtjaReject || status === loanStatus.gtjaPmtReject) {
    // CS Reject , Risk Reject ,GTJA Reject , GTJA Returned
    return apyResult.crgcg
  } else if (status === loanStatus.companyReject) {
    //  Company Returned , 
    return apyResult.cR
  } else if (status === loanStatus.noAccount) {
    // 
    return apyResult.noAccount
  }
}

function statusToAgree (status) {
  if (status === loanStatus.submitSignature) {
    // 
    return agreeResult.success
  } else if (status === loanStatus.waitOpsAppr) {
    // 
    return agreeResult.confirm
  } else if (status === loanStatus.clientCancel || status === loanStatus.syncClientCancel) {
    // 
    return agreeResult.fail
  } else if (status === loanStatus.waitRepayAppr) {
    // 
    return agreeResult.cashCheck
  } else if (status === loanStatus.waitStockAppr) {
    // 
    return agreeResult.stockCheck
  } else if (status === loanStatus.cancelRefused) {
    // 
    return agreeResult.cancelRefused
  } else if (status === loanStatus.cancelSucc) {
    // 
    return agreeResult.cancelSucc
  }
}

export default {loanStatus, repayStatus, repayTitle, repayType, repayMode, apyResult, agreeResult, statusToResult, statusToAgree, editStatus, resultStatus}



